import { Prop, Element, Method, h, Host, Component } from '@stencil/core';

export interface AccordionItem {
    item_id: string;
    label?: string;
    content?: string;
}

/**
 * @category Layout
 * @description A collapsible content container component that allows users to show/hide sections of content. Supports multiple panels, animations, and programmatic control of expanded states.
 *
 * @slot - Content is placed within the accordion wrapper. Used for placing atui-accordion-items if not using items prop.
 */

/**
 * ### ```interface AccordionItem```
 * ```
 * item_id: string;
 * label: string;
 * content: string;
 * ```
 * @category Layout
 * @description A collapsible content container component that allows users to show/hide sections of content. Supports multiple panels, animations, and programmatic control of expanded states.
 *
 * @slot - Content is placed within the accordion wrapper. Used for placing atui-accordion-items if not using items prop.
 */
@Component({
    tag: 'at-accordion',
    shadow: false,
})
export class AtAccordionComponent {
    @Element() el: HTMLElement;

    /**
     * Used to create accordion items.
     */
    @Prop() items: AccordionItem[] = [];

    /**
     * If set, all child accordions will be open by default.
     */
    @Prop() default_open: boolean = false;

    accordionItems: HTMLAtAccordionItemElement[] = [];

    async componentDidLoad() {
        // Wait for child components to be ready
        await new Promise((resolve) => setTimeout(resolve, 0));
        this.accordionItems = Array.from(
            this.el.querySelectorAll('at-accordion-item'),
        );
        if (this.default_open) {
            await this.openAll();
        }
    }

    /**
     * Toggles the open state of a specific accordion item
     * @param value Id of element to be toggled
     */
    @Method()
    async toggleById(value: string): Promise<void> {
        this.refreshAccordionItems();
        this.accordionItems.forEach((item) => {
            if (item.item_id === value) {
                if (item.open) {
                    item.closeAccordion();
                } else {
                    item.openAccordion();
                }
            }
        });
    }

    /**
     * Opens all accordion items
     */
    @Method()
    async openAll(): Promise<void> {
        this.refreshAccordionItems();
        this.accordionItems.forEach((item) => {
            item.openAccordion();
        });
    }

    /**
     * Closes all accordion items.
     */
    @Method()
    async closeAll(): Promise<void> {
        this.refreshAccordionItems();
        this.accordionItems.forEach((item) => {
            item.closeAccordion();
        });
    }

    /**
     * Toggles all accordion item's open state.
     */
    @Method()
    async toggleAll(): Promise<void> {
        this.refreshAccordionItems();
        this.accordionItems.forEach((item) => {
            if (item.open) {
                item.closeAccordion();
            } else {
                item.openAccordion();
            }
        });
    }

    private refreshAccordionItems(): void {
        this.accordionItems = Array.from(
            this.el.querySelectorAll('at-accordion-item'),
        );
    }

    private accordionId = `accordion-${Math.random().toString(36).substring(2, 11)}`;

    render() {
        return (
            <Host class="flex flex-col gap-2">
                <slot></slot>
                {this.items &&
                    this.items.map((item) => {
                        return (
                            <at-accordion-item
                                item_id={`${this.accordionId}-${item.item_id}`}
                                label={item.label}
                                content={item.content}
                            />
                        );
                    })}
            </Host>
        );
    }
}
